body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.transcript-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 100%;
}

.controls {
  padding: 15px;
  background-color: #f0f0f0;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.input-section {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.file-input,
.url-input {
  flex: 1;
  min-width: 200px;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.transcribe-btn {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.transcribe-btn:disabled {
  background-color: #cccccc;
}

.audio-player {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.transcript-container {
  flex-grow: 1;
  overflow-y: auto;
  background-color: #1a1a1a;
  color: white;
  padding: 40px;  /* Increased padding for better readability */
}

.sentence {
  font-size: 2rem;
  margin: 0;  /* Reset margin */
  padding: 25px 0;  /* Increased padding between lines */
  opacity: 0.5;
  transition: opacity 0.3s ease;
  line-height: 1.5;  /* Adjusted line height */
  text-align: left;  /* Changed from center to left */
  max-width: 1200px;  /* Maximum width for better readability */
  margin-left: auto;
  margin-right: auto;
}

.current-sentence {
  opacity: 1;
}

@media (max-width: 768px) {
  .sentence {
    font-size: 1.5rem;
    padding: 20px 0;  /* Slightly reduced padding for mobile */
  }
  
  .input-section {
    flex-direction: column;
  }

  .transcript-container {
    padding: 20px;  /* Reduced padding for mobile */
  }
}

.word {
  cursor: pointer;
  padding: 0 2px;
}

.word:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.bottom-panel {
  background-color: #2a2a2a;
  color: white;
  padding: 20px;
  min-height: 100px;
  max-height: 200px;
  overflow-y: auto;
}

.definition-section, .translation-section {
  margin-bottom: 15px;
}

.section-title {
  font-size: 0.9rem;
  color: #888;
  margin-bottom: 5px;
}

.content {
  font-size: 1.2rem;
}